import Mouse from './MouseHandle.js'
// import HISTORY from './HistoryHandler.js'
import { setCallback } from './../components/GraphicPoints.js'
import Key from './KeyHandler.js'
import  { cleanPointers } from './../components/SvgContainer.js'
import { isShapeSelectorActive } from './../components/SvgContainer.js'

// let historyElement = {}
// let canvas = null
// let database = null
const coordinates = {
    x1:null,y1:null,
    x3:null,y3:null
}
let polygon = null
let svg = null
let id = null

let save = () => {
    console.log('RectangleEditor Saved')
    cleanPointers()
    let polygon = null
    if(id === null){
        polygon = document.createElementNS('http://www.w3.org/2000/svg',"polygon");
    } else {
        polygon = document.querySelector(`[id=${id}]`)
    }
    
    polygon.setAttribute('fill', "none")
    polygon.setAttribute('stroke', "white")
    polygon.setAttribute('stroke-width', "1")
    polygon.id = 'id'+Math.round(new Date().getTime()/100)
    svg.appendChild(polygon)
    let points = `${coordinates.x1},${coordinates.y1} 
        ${coordinates.x3},${coordinates.y1} 
        ${coordinates.x3},${coordinates.y3} 
        ${coordinates.x1},${coordinates.y3}`
    polygon.setAttribute('points', points)

    polygon.addEventListener('mouseover', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        e.target.setAttribute('stroke-width', "10")
    })
    polygon.addEventListener('mouseout', (e)=>{
        e.target.setAttribute('stroke-width', "1")//get from history system
    })

    polygon.addEventListener('click', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        id = polygon.id
        rectangleEditor.getPath()
        document.querySelector('.p1').style.left = coordinates.x1+'px'
        document.querySelector('.p1').style.top = coordinates.y1+'px'
        document.querySelector('.p3').style.left = coordinates.x3+'px'
        document.querySelector('.p3').style.top = coordinates.y3+'px'
        rectangleEditor.drawRectangle()
        Key.addDownListener(13, save)
        setCallback(rectangleEditor.editor)
        e.target.setAttribute('stroke-width', "1")//get from history system
    })
}

class RectangleEditor{
    getPath(){
        if(polygon === null){
            polygon = document.createElementNS('http://www.w3.org/2000/svg',"polygon");
            polygon.setAttribute('fill', "none")
            polygon.setAttribute('stroke', "white")
            polygon.setAttribute('stroke-width', "1")
            polygon.classList.add('tmpNode')
            svg.appendChild(polygon)
        }   
    }
    drawRectangle(){
        polygon.style.display = null
        let points = `${coordinates.x1},${coordinates.y1} 
        ${coordinates.x3},${coordinates.y1} 
        ${coordinates.x3},${coordinates.y3} 
        ${coordinates.x1},${coordinates.y3}`
        polygon.setAttribute('points', points)
    }
    
    // showMonitor(){
    //     database.monitorGraph.x1.value = coordinates.x1
    //     database.monitorGraph.y1.value = coordinates.y1
    //     database.monitorGraph.x3.value = coordinates.x3
    //     database.monitorGraph.y3.value = coordinates.y3
    // }

    down(svgEvent){
        id  = null
        svg = svgEvent.target
        this.getPath()
        Key.addDownListener(13, save)
        // historyElement = HISTORY.getNewElement('RectangleEditor')
        coordinates.x1 = Mouse.getCursorPlusScroll(true).x
        coordinates.y1 = Mouse.getCursorPlusScroll(true).y
        Mouse.setMoveSubscriber(()=>{
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            this.drawRectangle()
        })
        Mouse.setUpSubscriber(()=>{
            document.querySelector('.p1').style.left = coordinates.x1+'px'
            document.querySelector('.p1').style.top = coordinates.y1+'px'
            document.querySelector('.p3').style.left = coordinates.x3+'px'
            document.querySelector('.p3').style.top = coordinates.y3+'px'
            // this.showMonitor()
            // database.setGraphicEditor([canvas, coordinates])
            // historyElement.c = coordinates
            // historyElement.ctx = {
            //     strokeStyle:polygon.getAttribute('stroke'),
            //     lineWidth:polygon.getAttribute('stroke-width')
            // }
            // HISTORY.updateElement(historyElement)
            Mouse.setMoveSubscriber(null)
            setCallback(rectangleEditor.editor)
        })
    }

    editor(element){
        if(element.className === 'p1'){
            coordinates.x1 = Mouse.getCursorPlusScroll(true).x
            coordinates.y1 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x1 = coordinates.x1
            // historyElement.c.y1 = coordinates.y1
            // HISTORY.updateElement(historyElement)
        } else if (element.className === 'p3'){
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x3 = coordinates.x3
            // historyElement.c.y3 = coordinates.y3
            // HISTORY.updateElement(historyElement)
        }
        rectangleEditor.drawRectangle()
    }
}

const rectangleEditor = new RectangleEditor()

export default rectangleEditor