import React, { useEffect } from 'react';
// import ThemeContext from './../context/ThemeContext';
import Mouse from './../objects/MouseHandle.js'
import './../css/GraphicEditor.css'


let callback = null
export let setCallback = (cb) => {
    callback = cb
}

export default function GraphicPoints() {
    // let context = null
    let p = []
    let clicked = null
    let follower = (e)=>{
        clicked.style.left = Mouse.getCursorPlusScroll(true).x+'px'
        clicked.style.top = Mouse.getCursorPlusScroll(true).y+'px' 
        if(callback !== null){
            callback(clicked)
        }
    }

    
    let down = (e) => {
        clicked = e.target
        Mouse.setMoveSubscriber(element => {follower(e)})
        Mouse.setUpSubscriber(()=>{
            clicked = null
            Mouse.setMoveSubscriber(null)
        })
    }
    
    // useEffect(() => {
        // context.setPointers({
        //     p1:p[0],
        //     p2:p[1],
        //     p3:p[2]
        // })
    // },[]);

    return (
        <div>
            <div className="p1" ref={elem =>(p[0] = elem)} onMouseDown={down}></div>
            <div className="p2" ref={elem =>(p[1] = elem)} onMouseDown={down}></div>
            <div className="p3" ref={elem =>{p[2] = elem;}} onMouseDown={down}></div>
            <div className="p4" ref={elem =>{p[3] = elem;}} onMouseDown={down}></div>
        </div>
    )
}
