import React from 'react'
import PathCreator from './PathCreator.js'
import Key from './../objects/KeyHandler.js'
import { isShapeMakerActive, isSingMakerActive } from './TopBar.js'
import curveEditor from './../objects/CurveEditor.js'
import rectangleEditor from './../objects/RectangleEditor.js'
import ellipseEditor from './../objects/EllipseEditor.js'
import Mouse from '../objects/MouseHandle.js';
import topBar from '../objects/TopBarHandler.js';

let shapeSelector = false

export let isShapeSelectorActive = () => {
    return shapeSelector
}

let setUpKeys = () => {
    Key.addDownListener(49, () => {
        if(isShapeMakerActive()){
            document.querySelector('#AiOutlineNodeIndex').style.backgroundColor = 'yellow'
        }        
    })
    Key.addUpListener(49, () => {
        document.querySelector('#AiOutlineNodeIndex').style.backgroundColor = null
    })

    Key.addDownListener(50, () => {
        if(isShapeMakerActive()){
            document.querySelector('#FaVectorSquare').style.backgroundColor = 'yellow'
        }        
    })
    Key.addUpListener(50, () => {
        document.querySelector('#FaVectorSquare').style.backgroundColor = null
    })

    Key.addDownListener(51, () => {
        if(isShapeMakerActive()){
            document.querySelector('#FaRegCircle').style.backgroundColor = 'yellow'
        }        
    })
    Key.addUpListener(51, () => {
        document.querySelector('#FaRegCircle').style.backgroundColor = null
    })

    
    // export let signSelector = false
    Key.addDownListener(52, () => {
        if(isShapeMakerActive()){
            document.querySelector('#GrCursor').style.backgroundColor = 'yellow'
            shapeSelector = true
        }

        // if(isSingMakerActive()){
        //     document.querySelector('#GrCursorKey').style.backgroundColor = 'yellow'
        //     signSelector = true
        // }
        
    })
    Key.addUpListener(52, () => {
        document.querySelector('#GrCursor').style.backgroundColor = null
        shapeSelector = false
        

        // document.querySelector('#GrCursorKey').style.backgroundColor = null
        // signSelector = false
    })

}


let FaPencilAltClick = (target)=>{//click
    // FaRegKeyboard
    topBar.clean('FaRegKeyboard')
    
    //return previouse state
    let display = (target.getAttribute('toggle-selected') === 'true')?'none':'block'
    if(display === 'none'){
        cleanPointers()
    }
    
    let graphics = document.querySelectorAll('[group=graphics]')
    graphics.forEach(e => {
        e.style.display = display
    })
}

topBar.addSubscriber('FaPencilAlt',FaPencilAltClick)

export let togglePen = () => {
    let element = document.querySelector('[id=FaPencilAlt]')
    FaPencilAltClick(element)
    topBar.toggle(element)
}

Key.addUpListener(68, () => {//[d]
    if(Key.isKeyPressed(16)//LEFT SHIFT
    // && Key.isKeyPressed(17)//LEFT CTRL 
    // && Key.isKeyPressed(18)//LEFT ALT
    ){
        togglePen()
        setUpKeys()
    }
})

topBar.addSubscriber('FaInfoCircle-FaPencilAlt',(target)=>{
    alert("English: Press key [1] and press main mouse button to draw a curve.\n"+
    "Then, press ENTER key to save the shape.\n"+
    "Espanol: Presione la tecla [1]  y presione el boton principal del raton para dibujar una curva.\n"+
    "Luego, presione la tecla ENTER para grabar la figura.")
})




export let cleanPointers = () => {
    document.querySelector('.p1').style.left = '0px'
    document.querySelector('.p1').style.top = '0px'
    document.querySelector('.p2').style.left = '0px'
    document.querySelector('.p2').style.top = '0px'
    document.querySelector('.p3').style.left = '0px'
    document.querySelector('.p3').style.top = '0px'
    document.querySelector('.p4').style.left = '0px'
    document.querySelector('.p4').style.top = '0px'
    let tmpNode = document.querySelectorAll('.tmpNode')
    if(tmpNode.length>0){
        tmpNode.forEach(tmp => {
            tmp.style.display = 'none'
        })
        document.querySelector('.p1').style.left = '0px'
        document.querySelector('.p2').style.left = '0px'
        document.querySelector('.p3').style.left = '0px'
        document.querySelector('.p4').style.left = '0px'
        Mouse.setUpSubscriber(null)//to avoid pointers when reopen pen tool
    }
    Key.addDownListener(13, null)
}

export default function SvgContainer() {
    let obj = []
    
    let down = (e) => {
        if(isShapeMakerActive()){
            if(Key.isKeyPressed(49)){
                cleanPointers()
                curveEditor.down(e)
            }
            if(Key.isKeyPressed(50) ){
                cleanPointers()
                rectangleEditor.down(e)
            }
            if(Key.isKeyPressed(51) ){
                cleanPointers()
                ellipseEditor.down(e)
            }
        }
    }
    
    return (
            <svg height="2000" width="2000" className="SvgContainer" xmlns="http://www.w3.org/2000/svg" onMouseDown={down}>
                {obj.map( element => {
                    return <PathCreator key={element.id} element={element} />
                })}
            </svg>
    )
}
