import React from 'react';
import KeyBoardBtn from './KeyBoardBtn.js'
import topBar  from '../objects/TopBarHandler.js';
import keyBoard  from '../objects/KeyBoardHandler.js';
import Key  from '../objects/KeyHandler.js';
import { isSingMakerActive } from './TopBar.js'
import './../css/KeyBoard.css'



let self = null
let buttons = '1234567890abcdefghijklmnopqrstuvwxyz'
let btn = Array.from(buttons)

topBar.addSubscriber('FaInfoCircle-FaRegKeyboard',(target)=>{
    alert("Easdfasd")
})

Key.addUpListener(87, () => {//[w]
    if(Key.isKeyPressed(16)//LEFT SHIFT
    ){
        click()
        let element = document.querySelector('[id=FaRegKeyboard]')
        topBar.toggle(element)
    }
})





let click = ()=>{
    let target = document.querySelector('[id=FaRegKeyboard]')
    topBar.clean('FaPencilAlt')
    let display = (target.getAttribute('toggle-selected') === 'true')?'none':'flex'
    document.querySelector('.KeyBoard').style.display = display
    let keys = document.querySelectorAll('[group=keys]')
    keys.forEach(e => {
        e.style.display = display
    })
    // console.log(display)
    if(display === 'flex'){
        for (let index = 48; index < 58; index++) {
            
            Key.addDownListener(index, () => {
                if(!isSingMakerActive()){
                    return
                }
                let id = `[id=btn-${(index-48)}]`
                // console.log(document.querySelector(id).childNodes[0].innerText )
                keyBoard.prepare(document.querySelector(id).childNodes[0].innerText)
                
            })
        }
        for (let index = 65; index < 91; index++) {
            
            Key.addDownListener(index, () => {
                if(!isSingMakerActive()){
                    return
                }
                let id = `[id=btn-${String.fromCharCode(index).toLowerCase()}]`
                // console.log(id)
                // console.log(document.querySelector(id).childNodes[0].innerText )
                keyBoard.prepare(document.querySelector(id).childNodes[0].innerText)
                
            })
        }
    }
}


export default function KeyBoard() {

    
    topBar.addSubscriber("FaRegKeyboard", click)
    // useEffect(() => {
    //     context.setDraw(self)
    // },[]);

    return (
        <div className="KeyBoard" ref={elem =>(self = elem)}>
            {btn.map((letter)=><KeyBoardBtn key={'btn-'+letter} content={letter}></KeyBoardBtn>)}
            <div>
                <div className="btn-name">&nbsp;</div>
                <div className="btn-content">+</div>
            </div>
        </div>
    )
}
