import Mouse from './MouseHandle.js'
// import { signSelector } from './../components/SvgContainer.js'
 

let  svg = null
let  text = null
class KeyBoardHandler
{
    constructor(){
        
    }
    getSvg(){
        svg = document.querySelector('.SvgContainer')
    }
    up(e){
        document.querySelector('.KeyBoard').style.display = 'flex'
    }

    prepare(string){
        
        this.getSvg()
        if(text === null){
            text = document.createElementNS('http://www.w3.org/2000/svg',"text")
        }
        
        text.setAttribute('x',Mouse.getCursorPlusScroll(true).x)
        text.setAttribute('y',Mouse.getCursorPlusScroll(true).y)
        text.setAttribute('fill', 'white');
        text.setAttribute('font-size', '30px');
        text.textContent = string
        text.addEventListener('mousedown', this.click)
        svg.appendChild(text)

        Mouse.setMoveSubscriber(()=>{
            text.setAttribute('x',Mouse.getCursorPlusScroll(true).x)
            text.setAttribute('y',Mouse.getCursorPlusScroll(true).y)
        })
        Mouse.setDownSubscriber(()=>{
            text = null
            Mouse.setMoveSubscriber(null)
        })
    }
    down(e){
        document.querySelector('.KeyBoard').style.display = 'none'
        this.getSvg()
        text = document.createElementNS('http://www.w3.org/2000/svg',"text")
        text.setAttribute('x',Mouse.getCursorPlusScroll(true).x)
        text.setAttribute('y',Mouse.getCursorPlusScroll(true).y)
        text.setAttribute('fill', 'white');
        text.setAttribute('font-size', '30px');
        text.textContent = e.target.previousSibling.innerText
        text.addEventListener('mousedown', this.click)
        // text.addEventListener('mouseover', this.over)
        // text.addEventListener('mouseout', this.over)
        svg.appendChild(text)
        Mouse.setMoveSubscriber(()=>{
            text.setAttribute('x',Mouse.getCursorPlusScroll(true).x)
            text.setAttribute('y',Mouse.getCursorPlusScroll(true).y)
        })
        Mouse.setUpSubscriber(()=>{
            this.up()
            Mouse.setMoveSubscriber(null)
        })
    }

    over(e){
        if(e.type === 'mouseover'){
            e.target.setAttribute('fill', '#66ccff');
        } else {
            e.target.setAttribute('fill', 'white');
        }
    }
    click(e){
        console.log('clikkkkkkkkkkkkkkkkkkkkkkkk')
        
        Mouse.setMoveSubscriber(()=>{
            console.log('setMoveSubscriber')
            e.target.setAttribute('x',Mouse.getCursorPlusScroll(true).x)
            e.target.setAttribute('y',Mouse.getCursorPlusScroll(true).y)
            e.target.setAttribute('fill', '#66ccff');
        })
        // }
        Mouse.setUpSubscriber(()=>{
            // e.target.setAttribute('fill', 'white');
            Mouse.setMoveSubscriber(null)
        })
    }
}

const keyBoard = new KeyBoardHandler()
export default keyBoard 