// import React from 'react'
import Mouse from './MouseHandle.js'
// import HISTORY from './HistoryHandler.js'
import { setCallback } from './../components/GraphicPoints.js'
import Key from './KeyHandler.js'
import  { cleanPointers } from './../components/SvgContainer.js'
import { isShapeSelectorActive } from './../components/SvgContainer.js'

// let canvas = null
// let database = null
// let strokeStyle = 'rgba(255, 255, 255, 1)'
// let historyElement = {}
// let ctx = null
const coordinates = {
    x1:null,y1:null,
    x2:null,y2:null,
    x3:null,y3:null,
    x4:null,y4:null,
}
let id = null
let svg = null
let path = null
let save = () => {
    console.log('CurveEditor Saved', id)
    cleanPointers()
    let path = null
    if(id === null){
        path = document.createElementNS('http://www.w3.org/2000/svg',"path")
    } else {
        path = document.querySelector(`[id=${id}]`)
    }
    
    path.setAttribute('fill', "none")
    path.setAttribute('stroke', "white")
    path.setAttribute('stroke-width', "1")
    path.id = 'id'+Math.round(new Date().getTime()/100)
    svg.appendChild(path)
    let d = `M ${coordinates.x1} ${coordinates.y1} 
        q ${(coordinates.x2-coordinates.x1)} ${(coordinates.y2-coordinates.y1)} 
        ${coordinates.x3-coordinates.x1} ${coordinates.y3-coordinates.y1}`
    path.setAttribute('d', d)

    path.addEventListener('mouseover', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        e.target.setAttribute('stroke-width', "10")
    })
    path.addEventListener('mouseout', (e)=>{
        e.target.setAttribute('stroke-width', "1")//get from history system
    })

    path.addEventListener('click', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        id = path.id
        curveEditor.getPath()
        document.querySelector('.p1').style.left = coordinates.x1+'px'
        document.querySelector('.p1').style.top = coordinates.y1+'px'
        document.querySelector('.p2').style.left = coordinates.x2+'px'
        document.querySelector('.p2').style.top = coordinates.y2+'px'
        document.querySelector('.p3').style.left = coordinates.x3+'px'
        document.querySelector('.p3').style.top = coordinates.y3+'px'
        coordinates.x4 = coordinates.x1-10
        coordinates.y4 = coordinates.y1
        document.querySelector('.p4').style.left = coordinates.x1-10+'px'
        document.querySelector('.p4').style.top = coordinates.y1+'px'


        curveEditor.drawPath()
        Key.addDownListener(13, save)
        setCallback(curveEditor.editor)
        e.target.setAttribute('stroke-width', "1")//get from history system
    })
}
class CurveEditor{
    getPath(){
        if(path === null){
            path = document.createElementNS('http://www.w3.org/2000/svg',"path");
            path.setAttribute('fill', "none")
            path.setAttribute('stroke', "white")
            path.setAttribute('stroke-width', "1")
            path.classList.add('tmpNode')
            svg.appendChild(path)
        }
    }

    // showMonitor(){
    //     database.monitorGraph.x1.value = coordinates.x1
    //     database.monitorGraph.y1.value = coordinates.y1
    //     database.monitorGraph.x2.value = coordinates.x2
    //     database.monitorGraph.y2.value = coordinates.y2
    //     database.monitorGraph.x3.value = coordinates.x3
    //     database.monitorGraph.y3.value = coordinates.y3
    // }

    // drawPathTmp(){
    //     path.style.display = null
    //     let d = `M ${coordinates.x1} ${coordinates.y1} 
    //     q ${(coordinates.x3-coordinates.x1)/2} ${(coordinates.y3-coordinates.y1)/2} 
    //     ${coordinates.x3-coordinates.x1} ${coordinates.y3-coordinates.y1}`
    //     path.setAttribute('d', d)
    // }
    drawPath(){
        path.style.display = null
        let d = `M ${coordinates.x1} ${coordinates.y1} 
                    q ${(coordinates.x2-coordinates.x1)} ${(coordinates.y2-coordinates.y1)} 
                    ${coordinates.x3-coordinates.x1} ${coordinates.y3-coordinates.y1}`
        path.setAttribute('d', d)
    }
    move(){
        Mouse.setMoveSubscriber(()=>{
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            coordinates.x2 = (coordinates.x1+Mouse.getCursorPlusScroll(true).x)/2
            coordinates.y2 = (coordinates.y1+Mouse.getCursorPlusScroll(true).y)/2
            this.drawPath()
        })
    }

    up(){
        Mouse.setUpSubscriber(()=>{
            document.querySelector('.p1').style.left = coordinates.x1+'px'
            document.querySelector('.p1').style.top = coordinates.y1+'px'
            document.querySelector('.p2').style.left = coordinates.x2+'px'
            document.querySelector('.p2').style.top = coordinates.y2+'px'
            document.querySelector('.p3').style.left = coordinates.x3+'px'
            document.querySelector('.p3').style.top = coordinates.y3+'px'


            document.querySelector('.p4').style.left = coordinates.x1-10+'px'
            document.querySelector('.p4').style.top = coordinates.y1+'px'
            // this.showMonitor()
            // historyElement.c = coordinates
            // historyElement.ctx = {
            //     strokeStyle:path.getAttribute('stroke'),
            //     lineWidth:path.getAttribute('stroke-width')
            // }
            // HISTORY.updateElement(historyElement)
            Mouse.setMoveSubscriber(null)
            setCallback(curveEditor.editor)
        })
    }
    down(svgEvent){
        id  = null
        svg = svgEvent.target// establece Contenedor
        this.getPath()//crea editor element
        Key.addDownListener(13, save)//registra Key ENTER Press event
        // historyElement = HISTORY.getNewElement('CurveEditor')
        coordinates.x1 = Mouse.getCursorPlusScroll(true).x
        coordinates.y1 = Mouse.getCursorPlusScroll(true).y
        coordinates.x4 = Mouse.getCursorPlusScroll(true).x-10
        coordinates.y4 = Mouse.getCursorPlusScroll(true).y
        this.move()
        this.up()
    }

    editor(element){//Pointers
        if(element.className === 'p1'){
            coordinates.x1 = Mouse.getCursorPlusScroll(true).x
            coordinates.y1 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x1 = coordinates.x1
            // historyElement.c.y1 = coordinates.y1
            // HISTORY.updateElement(historyElement)
        } else if (element.className === 'p2'){
            coordinates.x2 = Mouse.getCursorPlusScroll(true).x
            coordinates.y2 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x2 = coordinates.x2
            // historyElement.c.y2 = coordinates.y2
            // HISTORY.updateElement(historyElement)
        } else if (element.className === 'p3'){
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x3 = coordinates.x3
            // historyElement.c.y3 = coordinates.y3
            // HISTORY.updateElement(historyElement)
        } else if (element.className === 'p4'){
            let mx = coordinates.x4-Mouse.getCursorPlusScroll(true).x
            coordinates.x1 -= mx
            coordinates.x2 -= mx
            coordinates.x3 -= mx
            coordinates.x4 = Mouse.getCursorPlusScroll(true).x
            let my = coordinates.y4-Mouse.getCursorPlusScroll(true).y
            coordinates.y1 -= my
            coordinates.y2 -= my
            coordinates.y3 -= my
            coordinates.y4 = Mouse.getCursorPlusScroll(true).y

            document.querySelector('.p1').style.left = coordinates.x1+'px'
            document.querySelector('.p1').style.top = coordinates.y1+'px'
            document.querySelector('.p2').style.left = coordinates.x2+'px'
            document.querySelector('.p2').style.top = coordinates.y2+'px'
            document.querySelector('.p3').style.left = coordinates.x3+'px'
            document.querySelector('.p3').style.top = coordinates.y3+'px'
            
            // coordinates.y3++
            // historyElement.c.x3 = coordinates.x3
            // historyElement.c.y3 = coordinates.y3
            // HISTORY.updateElement(historyElement)
        }
        // // curveEditor.draw()
        curveEditor.drawPath()
    }

}

const curveEditor = new CurveEditor()

export default curveEditor