import Mouse from './MouseHandle.js'
// import HISTORY from './HistoryHandler.js'
import { setCallback } from '../components/GraphicPoints.js'
import Key from './KeyHandler.js'
import  { cleanPointers } from './../components/SvgContainer.js'
import { isShapeSelectorActive } from './../components/SvgContainer.js'

// let historyElement = {}
// let canvas = null
// let database = null

const coordinates = {
    x1:null,y1:null,
    x3:null,y3:null
}
let id = null
let svg = null
let ellipse = null
let save = () => {
    console.log('EllipseEditor Saved', id)
    cleanPointers()
    let ellipse = null
    if(id === null){
        ellipse = document.createElementNS('http://www.w3.org/2000/svg',"ellipse");
    } else {
        ellipse = document.querySelector(`[id=${id}]`)
    }
    
    
    ellipse.setAttribute('fill', "none")
    ellipse.setAttribute('stroke', "white")
    ellipse.setAttribute('stroke-width', "1")
    ellipse.id = 'id'+Math.round(new Date().getTime()/100)
    svg.appendChild(ellipse)

    
    ellipse.setAttribute('cx', (coordinates.x1+coordinates.x3)/2)
    ellipse.setAttribute('cy', (coordinates.y1+coordinates.y3)/2)
    ellipse.setAttribute('rx', Math.abs((coordinates.x3-coordinates.x1)/2))
    ellipse.setAttribute('ry', Math.abs((coordinates.y3-coordinates.y1)/2))


    ellipse.addEventListener('mouseover', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        e.target.setAttribute('stroke-width', "10")
    })

    ellipse.addEventListener('mouseout', (e)=>{
        e.target.setAttribute('stroke-width', "1")//get from history system
    })

    ellipse.addEventListener('click', (e)=>{
        if(!isShapeSelectorActive()){
            return
        }
        id = ellipse.id
        ellipseEditor.getPath()
        document.querySelector('.p1').style.left = coordinates.x1+'px'
        document.querySelector('.p1').style.top = coordinates.y1+'px'
        document.querySelector('.p2').style.left = coordinates.x2+'px'
        document.querySelector('.p2').style.top = coordinates.y2+'px'
        document.querySelector('.p3').style.left = coordinates.x3+'px'
        document.querySelector('.p3').style.top = coordinates.y3+'px'
        ellipseEditor.drawElipse()
        Key.addDownListener(13, save)
        setCallback(ellipseEditor.editor)
        e.target.setAttribute('stroke-width', "1")//get from history system
    })

}
class EllipseEditor{
    getPath(){
        if(ellipse === null){
            ellipse = document.createElementNS('http://www.w3.org/2000/svg',"ellipse");
            ellipse.setAttribute('fill', "none")
            ellipse.setAttribute('stroke', "white")
            ellipse.setAttribute('stroke-width', "1")
            ellipse.classList.add('tmpNode')
            svg.appendChild(ellipse)
        }   
    }

    // showMonitor(){
    //     database.monitorGraph.x1.value = coordinates.x1
    //     database.monitorGraph.y1.value = coordinates.y1
    //     database.monitorGraph.x3.value = coordinates.x3
    //     database.monitorGraph.y3.value = coordinates.y3
    // }
    

    drawElipse(){
        ellipse.style.display = null
        ellipse.setAttribute('cx', (coordinates.x1+coordinates.x3)/2)
        ellipse.setAttribute('cy', (coordinates.y1+coordinates.y3)/2)
        ellipse.setAttribute('rx', Math.abs((coordinates.x3-coordinates.x1)/2))
        ellipse.setAttribute('ry', Math.abs((coordinates.y3-coordinates.y1)/2))
    }
    

    down(svgEvent){
        id  = null
        svg = svgEvent.target
        this.getPath()
        Key.addDownListener(13, save)
        // historyElement = HISTORY.getNewElement('CircleEditor')
        coordinates.x1 = Mouse.getCursorPlusScroll(true).x
        coordinates.y1 = Mouse.getCursorPlusScroll(true).y
        Mouse.setMoveSubscriber(()=>{
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            this.drawElipse()
        })
        Mouse.setUpSubscriber(()=>{
            document.querySelector('.p1').style.left = coordinates.x1+'px'
            document.querySelector('.p1').style.top = coordinates.y1+'px'
            document.querySelector('.p3').style.left = coordinates.x3+'px'
            document.querySelector('.p3').style.top = coordinates.y3+'px'
            // this.showMonitor()
            // database.setGraphicEditor([canvas, coordinates])
            // historyElement.c = coordinates
            // historyElement.ctx = {
            //     strokeStyle:ellipse.getAttribute('stroke'),
            //     lineWidth:ellipse.getAttribute('stroke-width')
            // }
            // HISTORY.updateElement(historyElement)
            Mouse.setMoveSubscriber(null)
            setCallback(ellipseEditor.editor)
        })
    }

    editor(element){
        if(element.className === 'p1'){
            coordinates.x1 = Mouse.getCursorPlusScroll(true).x
            coordinates.y1 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x1 = coordinates.x1
            // historyElement.c.y1 = coordinates.y1
            // HISTORY.updateElement(historyElement)
        } else if (element.className === 'p3'){
            coordinates.x3 = Mouse.getCursorPlusScroll(true).x
            coordinates.y3 = Mouse.getCursorPlusScroll(true).y
            // historyElement.c.x3 = coordinates.x3
            // historyElement.c.y3 = coordinates.y3
            // HISTORY.updateElement(historyElement)
        }
        ellipseEditor.drawElipse()
    }
}

const ellipseEditor = new EllipseEditor()

export default ellipseEditor